* {
  box-sizing: border-box;
  font-family: "Palatino Linotype";
}

#searchBar {
  width: 100%;
  font-size: 16px;
  padding: 12px 20px 12px 40px;
  border: 1px solid #ddd;
  margin-bottom: 12px;
}

#playlistTable {
  border-collapse: collapse;
  width: 100%;
  border: 1px solid #ddd;
  font-size: 18px;
}
#playlistTable th,
#playlistTable td {
  text-align: left;
  padding: 12px;
}
th {
  border-bottom: #000000 dashed;
}
td {
  border-bottom: #000000 solid;
}
#playlistTable tr {
  border-bottom: 1px solid #ddd;
}
tr:nth-child(even) {
  background-color: #dddddd;
}
#playlistTable tr.header,
#playlistTable tr:hover {
  background-color: #f1f1f1;
}

button {
  margin: 5px;
}
.paginationBtns {
  background-color: black;
  color: white;
}
#tablePagination {
  display: flex;
}
#tablePaginationNavigator {
  text-align: center;
  flex: 70%;
}
#tablePaginationRowsPerPage {
  text-align: end;
  flex: 1;
}
